* {
  box-sizing: border-box;
}

:root {
  --box-shadow: #3d4e611a;
  --hover-shadow: #3d4e6133;
  --border: #e1e7ed;
  --placeholder: #3d4e61;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: bold;
}

.search {
  margin-top: 20px;
}

.search:hover {
  box-shadow: 0 4px 4px 0 var(--hover-shadow);
}

.search__big {
  height: 70px;
}

.search__small {
  height: 42px;
}

.search__input {
  border: 1px solid var(--border);
  box-shadow: 0 1px 8px 0 var(--box-shadow);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search__input-big {
  background-position: 26px;
  background-size: 19px 19px;
  padding-left: 62px;
  font-size: 16px;
}

.search__input-small {
  background-position: 13px;
  background-size: 11px 11px;
  padding-left: 33px;
  font-size: 14px;
}

.search__input::placeholder {
  color: var(--placeholder);
}

.search__input:focus {
  box-shadow: var(--hover-shadow);
  outline: none;
  font-family: Avenir, sans-serif;
  font-weight: 700;
}
